.scheduled {
    width: 100vw;
    height: auto;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.scheduled h1 {
    text-transform: uppercase;
    font-weight: 200;
}
.flex {
    display: flex;
    flex-direction: row;
    justify-content: center;
    width: 80vw; padding: 0vw 10vw;
    flex-wrap: wrap; gap: 25px;
}
.item {
    width: 200px;
    height: 260px;
    background: rgba(255, 255, 255, 0.267);
    backdrop-filter: blur(10px);
    border-radius: 5px;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
}
.item img {
    margin-top: 10px;
    border-radius: 5px;
}
.item span {
    text-align: left;
    width: 80%;
    padding: 0 10%;
    margin-top: 10px;
}
