.inputForm {
    width: 500px;
    height: 500px;
    justify-content: center;
    display: flex;
    flex-direction: column;
    background: rgba(255, 255, 255, 0.3);
    border-radius: 5px;
    margin: 0 auto;
    gap: 10px;
    align-items: center;
}
.clearImg {
    background: transparent; border: none; outline: none;
    color: black; font-weight: 200; margin-top: 10px;
}
.fileBox {
    margin-top: 10px;
    margin-bottom: 10px;
    display: flex; flex-direction: column;
    align-items: center; justify-content: center;
}
.formFlex {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 95vh;
    width: 100vw;
}
.uploadBtn {
    width: 200px;
    height: 45px;
    line-height: 45px;
    background: black;
    color: white;
    font-weight: 200;
    border-radius: 2.5px;
    text-align: center;
}
.btnSubmit {
    width: 150px;
    height: 45px;
    line-height: 45px;
    background: black;
    color: white;
    font-weight: 200;
    border-radius: 2.5px;
    text-align: center;
    border: none; outline: none;
}